import db from "libs/db"
import React from "react"
import { AiOutlineRight } from "react-icons/ai"
import cssVar from "views/mobileView/styles"

const Note = ({ type, goalDate }) => {
    const [dismissed, setDismissed] = React.useState({})
    React.useEffect(() => {
        db.get(db.model.dismissNotes).then(p => {
            setDismissed(p)
        })
    }, [])

    const onDismiss = () => {
        setDismissed({ ...dismissed, [type]: true })
        db.set(db.model.dismissNotes, JSON.stringify({ ...dismissed, [type]: true }))
    }

    const note = type === 'etf' ?
        {
            header: 'Important',
            body: 'Only ETFs invested in independently (and not as a part of a mintbox) are shown below'
        }
        :
        type === 'transactionETF' ?
            {
                header: 'Note',
                body: 'Transactions initiated on mintbox are shown below. Transactions initiated on other apps will not appear.'
            }
            :
            type === 'transactionMF' ?
                {
                    header: 'Note',
                    body: 'Transactions initiated on mintbox are shown below. Transactions initiated on other apps will not appear.'
                }
                :
                type === 'goal' ?
                    {
                        header: '',
                        body: 'You planned for this goal on '+ new Date(goalDate).toDateString()
                    }
                    :
                    {}
    return (
        dismissed?.[type] ?
            <></>
            :
            <div style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} onClick={() => onDismiss()}>
                <div style={{ padding: "2% 4%", backgroundColor: cssVar.getPropertyValue('--moderateRisk'), borderRadius: "8px" }}>
                    <div className='header2dark'>{note.header}</div>
                    <div className='smallTextLight' style={{ margin: "2px 0" }}>{note.body}</div>
                    <div className='header3dark secondaryGreen'> Tap to dismiss <AiOutlineRight size={12} /></div>
                </div>
            </div>
    )
}

export default Note