import db from 'libs/db';
import React from 'react';
import { toast } from "react-toastify";
import Modal from 'react-bootstrap/Modal';
import AdminServices from 'services/api/AdminServices';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import HomePortfolioCard from "views/mobileView/components/molecules/Cards/HomePortfolioCard";
import { HiOutlineExternalLink, HiOutlineHome } from 'react-icons/hi';
import { IoEllipsisVerticalCircleOutline } from 'react-icons/io5';
import { AiOutlineDown } from 'react-icons/ai';
import { BsDot } from 'react-icons/bs';
import ordinal_suffix_of from 'views/mobileView/components/atoms/ordinal-suffix';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import NotFound from './cards/NotFound';
import Note from './cards/Note';
import TransactionCard from './cards/TransactionCard';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import { formatDate } from 'views/mobileView/components/atoms/DateFormatter';

export default function FundScreen(props) {

  const [loading, setLoading] = React.useState(true);
  const [showMainModal, setShowMainModal] = React.useState(false)
  const [showModifySipModal, setShowModifySipModal] = React.useState(false)

  const [portfolio, setPortfolio] = React.useState({});
  const [portfolioPerformance, setPortfolioPerformance] = React.useState({});
  const [portfolioXIRR, setPortfolioXIRR] = React.useState({});
  const [transactions, setTransactions] = React.useState([]);
  const [user, setUser] = React.useState({});

  const FUNDCODE = props.location.state.FUNDCODE;
  const FOLIO = props.location.state.FOLIO;
  const FUNDDATA = props.location.state.FUNDDATA;
  // const FUNDDATA = portfolio?.FundsConsolidated?.find(f => f.FundCode === FUNDCODE && f.FolioNumber === FOLIO)

  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true)
    try {
      db.get(db.model.user).then(async (p) => {
        setUser(p.userData);
        await getPortfolio(p.userData.id)
        await getPortfolioXIRR(p.userData.id)
        await getPortfolioPerformance(p.userData.id)
        await getTransactions(p.userData.id)
        // await getScheduledOrders(p.userData.id)
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  };

  const getPortfolio = async (id) => {
    db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setPortfolio(aa)
          db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setPortfolio(p)
      }
    })
  }

  const getPortfolioPerformance = async (id) => {
    db.get(db.model.portfolioPerformance).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioPerformance(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolioPerformance API :", aa)
          setPortfolioPerformance(aa)
          db.set(db.model.portfolioPerformance, JSON.stringify(aa))
        }
      } else {
        setPortfolioPerformance(p)
      }
    })
  }

  const getPortfolioXIRR = async (id) => {
    db.get(db.model.portfolioXirr).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioXIRR(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          const aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio XIRR API :", aa)
          setPortfolioXIRR(aa)
          db.set(db.model.portfolioXirr, JSON.stringify(aa))
        }
      } else {
        setPortfolioXIRR(p)
      }
    })
  }

  const getTransactions = async (id) => {
    db.get(db.model.transactions).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getTransactions(id).then(res => res);
        if (res.type === true) {
          const aa = res.data;
          console.log("Get transactions API :", aa)
          setTransactions(aa)
          db.set(db.model.transactions, JSON.stringify(aa))
        }
      } else {
        setTransactions(p)
      }
    })
  }

  // const getScheduledOrders = async (id) => {
  //   db.get(db.model.sip).then(async (p) => {
  //     if (p === null) {
  //       let res = await AdminServices.getScheduledOrders(id).then(res => res);
  //       if (res.type === true) {
  //         const aa = res.data;
  //         console.log("Get Scheduled orders API :", aa)
  //         setSip(aa)
  //         db.set(db.model.sip, JSON.stringify(aa))
  //       }
  //     } else {
  //       setSip(p)
  //     }
  //   })
  // }

  const onPortfolioRefresh = async () => {
    await db.remove(db.model.portfolio);
    await db.remove(db.model.portfolioXirr);
    await db.remove(db.model.portfolioPerformance);
    await db.remove(db.model.assetAllocation);
    await db.remove(db.model.transactions)
    loadData();
  }

  const MainModal = () => {
    const [selectedOption, setSelectedOption] = React.useState()

    const onConfirm = () => {
      switch (selectedOption) {
        case 'redeem': {
          props.history.push({
            pathname: '/portfolio/redeem-mf',
            state: { fundData: FUNDDATA }
          })
          setShowMainModal(false);
          break;
        }
        case 'sip': {
          setShowMainModal(false);
          props.history.push({
            pathname: '/portfolio/confirm-invest',
            state: { fundData: FUNDDATA, type: 1, sip: true }
          })
          break;
        }
        default: setShowMainModal(false)
      }
    }
    return (
      <Modal
        className="bottom-drawer"
        show={showMainModal}
        onHide={() => setShowMainModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="sip" id="sip" />
            <label className="bodyTextDark radio-label" htmlFor="sip">Start a SIP</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="redeem" id="redeem" />
            <label className="bodyTextDark radio-label" htmlFor="redeem" style={{ color: cssVar.getPropertyValue('--peach') }}>Redeem</label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="outlineButton" onClick={() => setShowMainModal(false)}>
            Close
          </button>
          <button className="solidButton" onClick={() => onConfirm()}>
            Confirm
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  const ModifySipModal = () => {
    const [selectedOption, setSelectedOption] = React.useState()

    const onConfirm = () => {
      switch (selectedOption) {
        case 'editAmount': {
          setShowModifySipModal(false);
          break;
        }
        case 'editDate': {
          setShowModifySipModal(false);
          break;
        }
        case 'pauseSip': {
          setShowModifySipModal(false);
          break;
        }
        case 'stopSip': {
          setShowModifySipModal(false);
          break;
        }
        default: setShowModifySipModal(false)
      }
    }
    return (
      <Modal
        className="bottom-drawer"
        show={showModifySipModal}
        onHide={() => setShowModifySipModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="editAmount" id="editAmount" />
            <label className="bodyTextDark radio-label" htmlFor="editAmount">Edit SIP amount</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="editDate" id="editDate" />
            <label className="bodyTextDark radio-label" htmlFor="editDate">Edit SIP date</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="pauseSip" id="pauseSip" />
            <label className="bodyTextDark radio-label" htmlFor="pauseSip">Pause SIP</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="stopSip" id="stopSip" />
            <label className="bodyTextDark radio-label" htmlFor="stopSip" style={{ color: cssVar.getPropertyValue('--peach') }}>Stop SIP</label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="outlineButton" onClick={() => setShowModifySipModal(false)}>
            Close
          </button>
          <button className="solidButton" onClick={() => onConfirm()}>
            Confirm
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  const SIPs = () => {
    return (
      <div style={{ marginTop: '2%' }}>
        {
          portfolio?.ScheduledOrders?.filter(t => t.FundCode === FUNDCODE && t.FolioNumber === FOLIO).length === 0 ?
            <NotFound text="You don't have any active SIPs" buttonText='Start SIP now' onClick={() => {
              props.history.push({
                pathname: '/portfolio/confirm-invest',
                state: {
                  fundData: FUNDDATA,
                  type: 2,
                  sip: true
                }
              })
            }} />
            :
            portfolio?.ScheduledOrders?.filter(t=>t.FundCode === FUNDCODE && t.FolioNumber === FOLIO).map((t, i) => {
              return (
                <>
                  <div key={i} style={{ padding: "4%", borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
                    <div className='chip-sip' style={{ marginBottom: "1%" }}>
                      <BsDot size={24} color={t.IsDeleted === 1 ? cssVar.getPropertyValue('--red') : t.IsActive === 0 ? cssVar.getPropertyValue('--gold') : cssVar.getPropertyValue('--secondaryGreen')} style={{ marginLeft: "-10px" }} />
                      {t.IsDeleted === 1 ? 'Stopped' : t.IsActive === 0 ? 'Paused' : 'Active'}
                    </div>
                    <div style={{ display: "flex", alignItems: "center", gap: "6px" }}>
                      <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>
                          {ordinal_suffix_of(t.OrderDate)} of every month
                        </div>
                        <div className='header1dark'>{CurrencyFormat(t.Amount)}</div>
                      </div>
                      <div className='header2dark secondaryGreen' onClick={() => setShowModifySipModal(true)}>Modify <AiOutlineDown size={12} /></div>
                    </div>
                  </div>
                </>
              )
            })
        }
      </div>
    )
  }
  const STPs = () => {
    return (
      <div></div>
    )
  }

  const SWPs = () => {
    return (
      <div></div>
    )
  }

  const Transactions = () => {
    return (
      <>
        <Note type='transactionMF' />
        <div style={{ marginTop: "2%" }}>
          {
            transactions.filter(t => t.FundType === 2 && t.FundCode === FUNDCODE && t.FolioNumber === FOLIO).reverse().map((trx,i) => {
              return <TransactionCard trx={trx} i={i} type={2}/>
            })
          }
        </div>
      </>
    )
  }

  const MoreInfo = () => {
    return (
      <div style={{ marginTop: "2%" }}>
        <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
          <div style={{ flex: 1 }}>
            <div className='smallTextLight'>Folio number</div>
            <div className='bodyTextDark'>{FOLIO}</div>
          </div>
          <div style={{ flex: 1 }}>
            <div className='smallTextLight'>Category</div>
            <div className='bodyTextDark'>No data</div>
          </div>
        </div>
        <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
          <div style={{ flex: 1 }}>
            <div className='smallTextLight'>Units owned</div>
            <div className='bodyTextDark'>{FUNDDATA?.Units}</div>
          </div>
          <div style={{ flex: 1 }}>
            <div className='smallTextLight'>Exit load free units</div>
            <div className='bodyTextDark'>No data</div>
          </div>
        </div>
        <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
          <div style={{ flex: 1 }}>
            <div className='smallTextLight'>Exit load</div>
            <div className='bodyTextDark'>{FUNDDATA?.ExitLoad}</div>
          </div>
        </div>
      </div>
    )
  }


  const keyTitles = [
    { title: 'SIPs', view: SIPs(), show: true },
    { title: 'STPs', view: STPs(), show: false },
    { title: 'SWPs', view: SWPs(), show: false },
    { title: 'Transactions', view: Transactions(), show: true },
    { title: 'More info.', view: MoreInfo(), show: true }
  ];

  if (loading) {
    return (
      <ActivityLoaderMintbox />
    );
  }

  return (
    <div className='mainContainer'>
      <MainHeader
        navigation={props.history}
        header="Mutual fund"
      />
      <div className="dataContainer px-0">
        <div style={{ margin: "4% 4% 2%" }}>
          <div style={{ display: "flex", gap: "4%" }}>
            <AMCIcon amcName={FUNDDATA?.AMCName} width='40px' height='40px' />
            <div style={{ flex: 1 }}>
              <div className='header1dark'>{FUNDDATA?.SchemeName}</div>
              <div>
                <span className='smallTextLight'>NAV </span>
                <span className='smallTextDark'>{CurrencyFormat(FUNDDATA?.NAV, 'long', 2)}</span>
              </div>
            </div>
            <HiOutlineExternalLink size={24} className='secondaryGreen' onClick={() => props.history.push("/fund-detail/" + FUNDCODE)} style={{ alignSelf: 'center' }} />
          </div>
        </div>
        {
          FUNDDATA?.NAVDate?
          <div style={{position:'relative'}}>
          <span style={{position:'absolute', right:'8%', top:'40px'}} className='smallTextLight'>Nav as of {formatDate(FUNDDATA?.NAVDate)}</span>
          </div>
          :
          <></>
        }
        <HomePortfolioCard
          navigation={props.history}
          balance={FUNDDATA?.MFBalance}
          previousBalance={portfolio.PrevFundsConsolidated?.find(f => f.FundCode === FUNDCODE && f.FolioNumber === FOLIO)?.PrevMFBalance}
          xirr={portfolioXIRR.MFXIRR[FUNDCODE + ":" + FOLIO]?.XIRR}
          onRefresh={() => { onPortfolioRefresh() }}
          growthChartData={portfolioPerformance?.MutualFund?.filter(f => f.FundCode === FUNDCODE && f.FolioNumber === FOLIO)?.map(d => [new Date(d["NavDateStr"]).getTime(), d["Balance"]])}
        />
        <Tabs tabsObject={keyTitles.filter(k => k.show)} color={cssVar.getPropertyValue('--darkGreen')} />
      </div>
      <div className='bottom-nav'>
        <div className='bn-tab' onClick={() => { props.history.push('/dashboard') }}>
          <HiOutlineHome size={20} className='footerIcon' />
          <div className='footerText'>Home</div>
        </div>
        <div className='bn-tab' style={{ flex: 3 }}>
          <button
            className='solidButton'
            onClick={() => props.history.push({
              pathname: '/portfolio/confirm-invest',
              state: {
                fundData: FUNDDATA,
                type: 2
              }
            })}>
            Invest more
          </button>
        </div>
        <div className='bn-tab'>
          <IoEllipsisVerticalCircleOutline
            size='48'
            color={cssVar.getPropertyValue('--secondaryGreen')}
            onClick={() => setShowMainModal(true)} />
        </div>
      </div>
      <MainModal />
      <ModifySipModal />
    </div>
  );
}
